import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);


const getters = {
    doneTodos: state => {
        return state.todos.filter(todo => todo.done)
    }
}

export default new Vuex.Store({
    modules: {

    },
    state: {
        isLoading: false,
        isShowLogoutSpinner: true,
        appleColor: 'red',
        todos: [
            { id: 1, text: '...', done: true },
            { id: 2, text: '...', done: false },
            { id: 3, text: '...', done: true },
        ]
    },
    mutations: {
        changeText: function (state, payload) {
            console.log(payload);
            state.appleColor = payload.text;
        },
        addDoneTodos: function (state, payload) {
            console.log(payload);
            state.todos.push(payload);
        },
        aaa(state) {
            console.log(state);
        },
    },
    getters,

    /**
     * :  mutation 
     * 
     */
    strict: process.env.NODE_ENV !== "prod"
});
